import { TextField } from "@mui/material";
import { FC } from "react";
import useInput, { IInput } from "../../hooks/useInput";

interface InputGroupProps {
    name: IInput, 
    password: IInput, 
    key_access: IInput,
    isLogin: boolean,
    fullName: IInput
    passwordRepeat: IInput
}
 
const InputGroup: FC<InputGroupProps> = ({name, password, passwordRepeat, key_access, isLogin, fullName}) => {

if(isLogin){
    return ( 
        <div className="input_group">
            <div className="row">
                <TextField 
                    label="" 
                    variant="outlined" 
                    placeholder=' '
                    value={name.value}
                    error={name.error}
                    onChange={name.onChange}
                />
            </div>
            <div className="row">
                <TextField 
                    error={password.error}
                    label="" 
                    variant="outlined" 
                    placeholder=' '
                    value={password.value}
                    onChange={password.onChange}
                    type="password"
                />
            </div>
        </div>
    );
}

return (
    <div className="input_group">
            <div className="row">
                <TextField 
                    error={name.error}
                    label="" 
                    variant="outlined" 
                    placeholder=' '
                    value={name.value}
                    onChange={name.onChange}
                />
            </div>
            <div className="row">
                <TextField 
                    error={fullName.error}
                    label=" " 
                    variant="outlined" 
                    placeholder=' '
                    value={fullName.value}
                    onChange={fullName.onChange}
                />
            </div>
            <div className="row">
                <TextField 
                    error={password.error}
                    label="" 
                    variant="outlined" 
                    placeholder=' '
                    value={password.value}
                    onChange={password.onChange}
                    type="password"
                />
            </div>
            <div className="row">
                <TextField 
                error={passwordRepeat.error}
                    label=" " 
                    variant="outlined" 
                    placeholder=' '
                    value={passwordRepeat.value}
                    onChange={passwordRepeat.onChange}
                    type="password"
                />
            </div>
            <div className="row">
                <TextField 
                    error={key_access.error}
                    label=" " 
                    variant="outlined" 
                    placeholder='  '
                    value={key_access.value}
                    onChange={key_access.onChange}
                />
            </div>
        </div>
)
        
}
 
export default InputGroup;